<template>
    <div class="container">
        <div v-if="!isLoaded">每日19点更新数据...</div>
        <echarts class="ec-canvas" :echartsData="echartsData" @passage_data="getWechatInfo"/>
        <!-- 表格信息头 -->
        <div class="sheet_header" v-if="isLoaded">
            <span class="sh_header" style="font-weight: bold;">热门推送详情列表--    </span>
            <span style="font-weight: bold;">品牌:</span>
            <span>{{brand}}    </span>
            <span style="font-weight: bold;"> 时间:</span>
            <span> {{date}}</span>
        </div>

        <div class="passage" v-if="clicked">
            <div class="table">
                <div class="tr bg-w">
                    <div class="th title">微信文章</div>
                    <div class="th">公众号</div>
                    <div class="th">阅读数</div>
                    <div class="th">再看数</div>
                </div>
                <div v-for="(item, index) in wechat_content">
                    <div class="tr bg-g" v-if="(index % 2) == 0">
                        <div class="td title">{{item.title}}</div>
                        <div class="td">{{item.account}}</div>
                        <div class="td">{{item.read_num}}</div>
                        <div class="td">{{item.like_num}}</div>
                    </div>
                    <div class="tr" v-else>
                        <div class="td title">{{item.title}}</div>
                        <div class="td">{{item.account}}</div>
                        <div class="td">{{item.read_num}}</div>
                        <div class="td">{{item.like_num}}</div>
                    </div>
                </div>
            </div>

        </div>
<!--    <div id="test">-->

    </div>
</template>

<script>
    import echarts from '@/components/echarts/index'
    export default {
        name: "test",
        components: {
            echarts
        },
        beforeCreate(){
            // this.$store.dispatch("get_data")
            // console.log(this.$store.state.database)


        },
        created(){
            var that = this
            this.$store.dispatch("get_data")
            var interval = setInterval(() => {
                // console.log(that.$store.state.flag)
                if (that.$store.state.flag){
                    that.echartsData = {
                        baseOption:{
                            timeline: {
                                axisType: "category",
                                autoPlay:true,
                                playInterval: 3000,
                                data: Object.keys(this.$store.state.database),
                                inverse: true,
                                rewind: true,
                                label: {
                                    position: "auto",
                                    fontSize: 10,
                                    interval: 1
                                },
                                controlStyle: {
                                    itemGap:5,
                                    itemSize: 18
                                },
                                left:20,
                                right: 20

                            },
                            color: ['#37a2da', '#32c5e9', '#67e0e3'],
                            tooltip: {
                                trigger: 'axis',
                                axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                                    type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                                }
                            },
                            title: {
                                text: "汽车品牌微信热度统计",
                                // subtext: "2020-04-10"
                            },
                            legend: {
                                data: ['提及品牌的热门推送数量'],
                                top: "25"
                            },
                            grid: {
                                left: 50,
                                right: 20,
                                bottom: 60,
                                top: 60,
                                containLabel: true
                            },
                            xAxis: {
                                type: "value",
                                position: "top",
                                axisLine: {
                                    lineStyle: {
                                        color: '#999',
                                        show:false
                                    }
                                },
                                axisLabel: {
                                    color: '#666',
                                    show: false
                                }
                            },
                            yAxis:
                                {
                                    type: 'category',
                                    axisTick: { show: false },
                                    boundaryGap: true,
                                    // data: this.$store.state.x_data,
                                    axisLine: {
                                        lineStyle: {
                                            color: '#999'
                                        }
                                    },
                                    // axisLine: {onZero: false},
                                    axisLabel: {
                                        color: '#666'
                                    }
                                },
                            series:[
                                {
                                    name: '提及品牌的热门推送数量',
                                    type: "bar",
                                    label: {
                                        normal: {
                                            show: true,
                                            position: 'inside'
                                        }
                                    },}
                            ],
                            dataZoom: [
                                {
                                    // id: 'dataZoomY',
                                    type: 'slider',
                                    left: "5%",
                                    yAxisIndex: 0,
                                    filterMode: 'empty', // 设定为 'filter' 从而 X 的窗口变化会影响 Y 的范围。
                                    // rangeMode: ["0", "0"]
                                    start: 60,
                                    end: 100
                                }
                            ]
                        },
                        options: [
                            {
                                title:{
                                    subtext:Object.keys(this.$store.state.database)[0]
                                },
                                xAxis:[{
                                    type: "value",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[0]]["count"]
                                }],
                                yAxis:[{
                                    type: "category",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[0]]["brands"]
                                }],
                                series:[
                                    {type:"bar",data:this.$store.state.database[Object.keys(this.$store.state.database)[0]]["count"]}
                                ]
                            },
                            {
                                title:{
                                    subtext:Object.keys(this.$store.state.database)[1]
                                },
                                xAxis:[{
                                    type: "value",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[1]]["count"]
                                }],
                                yAxis:[{
                                    type: "category",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[1]]["brands"]
                                }],
                                series:[
                                    {type:"bar",data:this.$store.state.database[Object.keys(this.$store.state.database)[1]]["count"]}
                                ]
                            },      {
                                title:{
                                    subtext:Object.keys(this.$store.state.database)[2]
                                },
                                xAxis:[{
                                    type: "value",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[2]]["count"]
                                }],
                                yAxis:[{
                                    type: "category",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[2]]["brands"]
                                }],
                                series:[
                                    {type:"bar",data:this.$store.state.database[Object.keys(this.$store.state.database)[2]]["count"]}
                                ]
                            },      {
                                title:{
                                    subtext:Object.keys(this.$store.state.database)[3]
                                },
                                xAxis:[{
                                    type: "value",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[3]]["count"]
                                }],
                                yAxis:[{
                                    type: "category",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[3]]["brands"]
                                }],
                                series:[
                                    {type:"bar",data:this.$store.state.database[Object.keys(this.$store.state.database)[3]]["count"]}
                                ]
                            },      {
                                title:{
                                    subtext:Object.keys(this.$store.state.database)[4]
                                },
                                xAxis:[{
                                    type: "value",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[4]]["count"]
                                }],
                                yAxis:[{
                                    type: "category",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[4]]["brands"]
                                }],
                                series:[
                                    {type:"bar",data:this.$store.state.database[Object.keys(this.$store.state.database)[4]]["count"]}
                                ]
                            },      {
                                title:{
                                    subtext:Object.keys(this.$store.state.database)[5]
                                },
                                xAxis:[{
                                    type: "value",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[5]]["count"]
                                }],
                                yAxis:[{
                                    type: "category",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[5]]["brands"]
                                }],
                                series:[
                                    {type:"bar",data:this.$store.state.database[Object.keys(this.$store.state.database)[5]]["count"]}
                                ]
                            },      {
                                title:{
                                    subtext:Object.keys(this.$store.state.database)[6]
                                },
                                xAxis:[{
                                    type: "value",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[6]]["count"]
                                }],
                                yAxis:[{
                                    type: "category",
                                    data:this.$store.state.database[Object.keys(this.$store.state.database)[6]]["brands"]
                                }],
                                series:[
                                    {type:"bar",data:this.$store.state.database[Object.keys(this.$store.state.database)[6]]["count"]}
                                ]
                            },
                        ],

                    }
                    clearInterval(interval)
                    that.isLoaded = true
                }
            }, 500)
            // console.log(this.$store.state.database)


        },
        data() {
            return {
                echartsData: {},
                wechat_content: [],
                clicked: false,
                date: '(点击时间轴)',
                brand:'(点击统计图表项目结果)',
                // wechat_info: [],
                isLoaded: false


            }
        },
        methods: {
            getWechatInfo(info, date, brand, clicked) {
                this.wechat_content = info
                this.date = date
                this.brand = brand
                this.clicked = clicked
                // console.log(this.brand)
            }
        }

    }
</script>

<style scoped>
    .container{
        position: absolute;
        top: 10px;
        width: 100%;
        height: 70%;
        padding:0;
    }
    ec-canvas {
        position: relative;
        width:100%;
        height: 100%;
        /* top: 0; */
        /* top: 5px;
        bottom: 100px;
        left: 0;
        right: 0; */
    }
    .passage {
        position: relative;
        /* bottom: 0px; */
    }
    /* button {
      margin: 10px;
    } */
    .sheet_header{
        font-size: 0.7rem;
        /* position: flex;
        width: 100%;
        justify-content: left;
        height: 1.5rem;

        margin-top: 5px;
        padding-left: 5px;
        font-weight: bold; */
    }

    .table {
        border: 0px solid darkgray;
        padding: 3rpx 3rpx;
        font-size: 0.7rem;
    }
    .tr {
        display: flex;
        width: 100%;
        justify-content: center;
        height: 4rem;
        align-items: center;
    }

    .td {
        width:40%;
        justify-content: center;
        text-align: center;
    }
    .bg-w{
        background: #0089dd;
        opacity: 0.8;
        height: 1.5rem;
    }
    .bg-g{
        background: #E6F3F9;
    }
    .th {
        width: 30%;
        justify-content: center;
        background: #0089bb;
        color: #fff;
        display: flex;
        height: 1.5rem;
        align-items: center;
        font-size: 0.8rem;
    }
    .title{
        width:80%
    }

</style>
