import Vue from 'vue'
import Vuex from 'vuex'
import {getMultidata} from "@/network/auto_data"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    database: {},
    passagebase: null,
    flag: false

  },
  mutations: {
    change_database(state, payload){
      state.database = payload
    },
    change_passagebase(state, payload){
      state.passagebase = payload
      state.flag = true
    },
    change_test(state, payload){
      state.test = 1
    },
    refresh_database(state) {
      state.database = null
    }
  },
  actions: {
    get_data(context){
      getMultidata().then((res) => {
        // console.log("actions")
        var database = {}
        var passagebase = {}
        var date = []
        context.commit('refresh_database',database)
        // var brand_day =[]
        // var count_day = []
        for (var date=0; date< res.data.length; date++){
          var brand_day =[]
          var count_day = []
          var passage_day = {}
          var sub_database = {}
          for (var i=0; i<res.data[date]['brands'].length;i++){
            // console.log(res.data[date]['brands'][i]["brand"])
            brand_day.push(res.data[date]['brands'][i]["brand"])
            count_day.push(res.data[date]['brands'][i]["count"])
            passage_day[res.data[date]['brands'][i]["brand"]] = res.data[date]['brands'][i]["passage"]
          }
          brand_day.reverse()
          count_day.reverse()
          sub_database["brands"] = brand_day
          sub_database["count"] = count_day
          database[res.data[date]["date"]] = sub_database
          passagebase[res.data[date]["date"]] = passage_day
        }
        // console.log(database)
        context.commit('change_database',database)
        context.commit('change_passagebase',passagebase)
        // console.log(app.globalData.passagebase)
        // console.log(app.globalData.database[Object.keys(app.globalData.database)[0]]["count"])
        // that.init()
      })
    },
    test(context){
      context.commit("change_test")
    }
  },
  modules: {
  }
})
