<template>
    <div :class="className" :style="{height:height,width:width}" @click="emitToP" />
</template>

<script>
    import echarts from 'echarts'
    // require('echarts/theme/macarons') // echarts theme
    import {debounce} from '@/utils/index.js'
    import {getMultidata} from "@/network/auto_data"
    // const animationDuration = 6000

    export default {
        props: {
            className: {
                type: String,
                default: 'chart'
            },
            width: {
                type: String,
                default: '100%'
            },
            height: {
                type: String,
                default: '100%'
            },
            // 数据源
            echartsData: {
                type: Object,
                default: {}
            },
        },
        data() {
            return {
                chart: null,
                clicked: false,
                date: '(点击时间轴)',
                brand:'(点击统计图表项目结果)',
                wechat_info: [],
                isLoaded: false
            }
        },
        watch: {
        },
        created(){


            // this.getData()
            // this.$nextTick()

        },
        //初始化
        mounted() {
            var that =this
            // console.log("component is running")
            var interval = setInterval(() => {
                // console.log(that.$store.state.flag)
                if (Object.keys(that.echartsData).length){
                    clearInterval(interval)
                    // console.log(that.echartsData)
                    that.initChart()
                    that.isLoaded = true
                }
            }, 200)
            // console.log(this.echartsData)

            // console.log(this.chart)
            this.resizeHandler = debounce(() => {
                if (this.chart) {
                    this.chart.resize()
                }
            }, 100)
            window.addEventListener('resize', this.resizeHandler)
        },
        //销毁
        beforeDestroy() {
            if (!this.chart) {
                return
            }
            window.removeEventListener('resize', this.resizeHandler)
            this.chart.dispose()
            this.chart = null
        },
        methods: {
            initChart() {
                this.chart = echarts.init(this.$el)
                this.chart.setOption(this.echartsData)
                var that = this
                // var _that = this
                this.chart.on("click","series.bar", function(params) {
                    var date = Object.keys(that.$store.state.database)[that.chart._model.option.timeline[0].currentIndex]
                    var brand = params.name
                    that.clicked = true
                    that.date = date
                    that.brand = brand
                    that.wechat_info = that.$store.state.passagebase[date][brand]
                    // console.log(that.chart._model.option.timeline[0].currentIndex)
                })
                return this.chart
            },
            emitToP() {
                this.$emit("passage_data", this.wechat_info, this.date, this.brand, this.clicked)
            },

            // getData() {
            //     this.$store.dispatch("get_data")
            //     getMultidata().then(res => {
            //         console.log(res)
            //
            //         // this.$store.dispatch("test")
            //         // console.log(this.$store.state.test)
            //         console.log(this.$store.state.database)
            //     })
            // }
        }
    }
</script>


